import { getDefaultOptions, request } from 'api/helpers';

const resource = 'tasks';

export const apiTaskGet = async (serviceUrl, taskId) => {
  const url = `${serviceUrl}/${resource}/${taskId}`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiTaskActionPost = async (serviceUrl, taskId, payload) => {
  const url = `${serviceUrl}/${resource}/${taskId}/actions/bulk`;
  const options = {
    ...getDefaultOptions(),
    method: 'POST',
    body: payload ? JSON.stringify(payload) : null,
  };
  return request(url, options);
};

export const apiActionGet = async (serviceUrl, taskId) => {
  const url = `${serviceUrl}/${resource}/${taskId}/actions`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiActionPut = async (serviceUrl, action, typeAction = 'action') => {
  const { id } = action;
  const url = `${serviceUrl}/${typeAction === 'subAction' ? 'subactions' : 'actions'}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'PUT',
    body: action ? JSON.stringify(action) : null,
  };
  return request(url, options, false);
};
