import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { apiActionGet, apiTaskGet } from '../../api/action';
import { mockActions, mockTask } from '../../components/__mocks__/mocks';
import { getUniqueUsers } from '../../helpers/helper';
import AppManager from '../app/AppManager';
import LoadingManager from '../loading/LoadingManager';
import UserManager from '../user/UserManager';
import ActionContext from './ActionContext';

const ActionProvider = ({ children, taskId }) => {
  const [state, setState] = useState({
    taskId,
    task: {},
    actions: [],
    error: null,
    message: null,
    load: false,
  });
  const [completed, setCompleted] = useState(false);
  const { keycloak, serviceUrl, useMock } = AppManager();
  const { setLoading } = LoadingManager();
  const { t } = useTranslation();
  const { setUsersPresent } = UserManager();

  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoading(true);
        let task;
        let actions;
        if (useMock) {
          [task, actions] = await new Promise(resolve =>
            setTimeout(() => resolve([mockTask, mockActions]), 100)
          );
        } else {
          [task, actions] = await Promise.all([
            apiTaskGet(serviceUrl, taskId),
            apiActionGet(serviceUrl, taskId),
          ]);
        }
        const usersActions = getUniqueUsers(actions.payload);

        setUsersPresent(usersActions);
        actions = actions.payload.map(m => (m.subActions ? m : { ...m, subActions: [] }));

        setState(prev => ({ ...prev, task: task.payload, actions }));
      } catch (err) {
        console.error(err);
        setState(prevState => ({ ...prevState, error: t('error.dataLoading') }));
      } finally {
        setLoading(false);
      }
    };
    const authenticated = keycloak && keycloak.initialized && keycloak.authenticated;
    if (authenticated && !completed) {
      fetchData();
      setCompleted(true);
    }
  }, [keycloak, useMock]);

  return <ActionContext.Provider value={[state, setState]}> {children} </ActionContext.Provider>;
};

ActionProvider.propTypes = {
  children: PropTypes.oneOfType([PropTypes.arrayOf(PropTypes.node), PropTypes.node]).isRequired,
  taskId: PropTypes.string.isRequired,
};

export default ActionProvider;
