import { useContext } from 'react';
import { useTranslation } from 'react-i18next';
import { apiActionPut, apiActionGet, apiTaskActionPost } from '../../api/action';
import AppManager from '../app/AppManager';
import loadingManager from '../loading/LoadingManager';
import ActionContext from './ActionContext';

const ActionManager = (keycloak, serviceUrl) => {
  const [state, setState] = useContext(ActionContext);
  const { setLoading } = loadingManager();
  const { useMock } = AppManager();
  const { t } = useTranslation();

  const clearError = () => setState(prev => ({ ...prev, error: null }));

  const clearMessage = () => setState(prev => ({ ...prev, message: null }));

  const addAction = async payload => {
    try {
      setLoading(true);
      if (keycloak.initialized && keycloak.authenticated) {
        if (useMock) {
          const response = await new Promise(
            resolve =>
              setTimeout(() =>
                resolve(payload.flatMap(action => (action.id ? action : { ...action, id: 99 })))
              ),
            1500
          );
          setState(prev => ({
            ...prev,
            actions: [...response],
            message: t('message.actionUpdated'),
          }));
        } else {
          const response = await apiTaskActionPost(serviceUrl, state.task.id, payload);
          if (response) {
            const actions = await apiActionGet(serviceUrl, state.task.id);
            const newActions = actions.payload.flatMap(action =>
              action.subActions ? action : { ...action, subActions: [] }
            );
            setState(prev => ({
              ...prev,
              actions: [...newActions],
              message: t('message.actionUpdated'),
            }));
          }
        }
      }
    } catch (error) {
      console.error(error);
      setState({ ...state, error: t('error.dataLoading'), message: null });
    } finally {
      setLoading(false);
    }
    return null;
  };

  const updateAction = async (typeAction, payload) => {
    try {
      setLoading(true);
      if (keycloak.initialized && keycloak.authenticated) {
        const response = await apiActionPut(serviceUrl, payload, typeAction);
        if (response) {
          const actions = await apiActionGet(serviceUrl, state.task.id);
          // console.log('apiActionGet', actions);
          const newActions = actions.payload.flatMap(action =>
            action.subActions ? action : { ...action, subActions: [] }
          );
          setState(prev => ({
            ...prev,
            actions: [...newActions],
            message: t('message.actionUpdated'),
          }));
        }
        return response;
      }
    } catch (error) {
      console.error(error);
      setState({ ...state, error: t('error.dataLoading'), message: null });
    } finally {
      setLoading(false);
    }
    return null;
  };

  return {
    task: state.task,
    contextActions: state.actions,
    addAction,
    updateAction,
    error: state.error,
    message: state.message,
    clearError,
    clearMessage,
  };
};

export default ActionManager;
