import { getDefaultOptions, request } from 'api/helpers';

const resource = 'users';

export const apiUsersGet = async (
  serviceUrl,
  pagination = {
    page: 0,
    pageSize: 10,
  },
  search,
  informedOrConsulted = false
) => {
  let url = `${serviceUrl}/${resource}?informedOrConsulted=${informedOrConsulted}&sortBy=lastname,firstname,id`;
  if (pagination) {
    url += `&page=${pagination.page}&size=${pagination.pageSize}`;
  }
  if (search) {
    url += `&globalSearch=${search}`;
  }
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiUserFromFiscalCodeGet = async (serviceUrl, fiscalCode) => {
  const url = `${serviceUrl}/${resource}/fiscalCode/${fiscalCode}`;

  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};
