import { Box, Button, Grid, Typography } from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import DragIndicatorIcon from '@material-ui/icons/DragIndicator';
import InsertCommentIcon from '@material-ui/icons/InsertComment';
import { uniqueId } from 'lodash';
import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { actionInfoStyle } from './style/actionsStyle';

const ActionInfo = ({
  actions,
  handleClickAction,
  disabledButton,
  loadInfo,
  infoSelected,
  disabledEvents,
}) => {
  const css = actionInfoStyle();
  const { t } = useTranslation();

  const handleClick = index => {
    handleClickAction('subAction', index);
  };

  return actions.map((action, index) => {
    return (
      <Box key={uniqueId('action-info_')}>
        <Grid container>
          <Grid item xs={1} classes={{ item: css.item }}>
            <Box className={css.boxCircle}>
              <Box className={`${css.circle} `} />
            </Box>
          </Grid>
          <Grid item xs={11} classes={{ item: css.item }}>
            <Box
              className={`${css.boxAction} ${
                infoSelected?.action?.id === action.id ? css.boxActionSelected : ''
              }`}
            >
              <Box marginLeft={2} marginRight={2} className={css.iconAction}>
                <InsertCommentIcon fontSize="small" />
              </Box>
              <Box
                className={css.boxTextAction}
                onClick={() => loadInfo('action', action, index)}
                style={{
                  pointerEvents: `${disabledEvents ? 'none' : 'auto'}`,
                }}
              >
                <Typography variant="body1" color="textPrimary">
                  {`${action.title || 'Azione'} `}
                </Typography>
              </Box>
            </Box>
          </Grid>
          {action?.subActions?.map(subAction => (
            <React.Fragment key={uniqueId('sub-action_')}>
              <Grid key={uniqueId('sub-action_')} item xs={1} classes={{ item: css.item }} />
              <Grid item xs={11} classes={{ item: css.item }}>
                <Box
                  className={`${css.boxAction} ${
                    infoSelected?.action?.id === subAction.id ? css.boxActionSelected : ''
                  }`}
                >
                  <Box className={css.boxIconSubAction}>
                    <DragIndicatorIcon fontSize="small" />
                  </Box>
                  <Box marginRight={2} className={css.iconAction}>
                    <InsertCommentIcon fontSize="small" />
                  </Box>
                  <Box
                    className={css.boxTextAction}
                    onClick={() => loadInfo('subAction', subAction, index)}
                    style={{
                      pointerEvents: `${disabledEvents ? 'none' : 'auto'}`,
                    }}
                  >
                    <Typography variant="body1" color="textPrimary">
                      {`${subAction.title}`}
                    </Typography>
                  </Box>
                  <Box marginRight={2} className={css.iconAction}>
                    <ChevronRightIcon fontSize="small" />
                  </Box>
                </Box>
              </Grid>
            </React.Fragment>
          ))}

          <Grid item xs={1} classes={{ item: css.item }} />
          <Grid item xs={11} classes={{ item: css.item }}>
            <Box className={css.boxAddSubAction}>
              <Button
                variant="outlined"
                type="button"
                className={css.buttonAddSubAction}
                startIcon={<AddIcon />}
                onMouseDown={() => handleClick(index)}
                disabled={disabledButton}
              >
                <Typography variant="body2" color="textSecondary">
                  {t('entities.update.actions.addSubaction')}
                </Typography>
              </Button>
            </Box>
          </Grid>
        </Grid>
      </Box>
    );
  });
};

ActionInfo.propTypes = {
  actions: PropTypes.arrayOf(
    PropTypes.shape({
      title: PropTypes.string,
      subActions: PropTypes.arrayOf(PropTypes.shape({})),
    })
  ).isRequired,
  handleClickAction: PropTypes.func.isRequired,
  disabledButton: PropTypes.bool.isRequired,
  loadInfo: PropTypes.func.isRequired,
  infoSelected: PropTypes.shape({}),
  disabledEvents: PropTypes.bool.isRequired,
};

export default ActionInfo;
