import { Avatar, Box, Grid, IconButton, Typography } from '@material-ui/core';
import CreateIcon from '@material-ui/icons/Create';
import PropTypes from 'prop-types';
import AddIcon from '@material-ui/icons/Add';
import DeleteIcon from '@material-ui/icons/Delete';
import React, { useEffect, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { isEmpty } from 'lodash';
import ActionManager from '../../../context/action/ActionManager';
import UserManager from '../../../context/user/UserManager';
import DialogUsers from '../../Dialog/Users/DialogUsers';
import InputDatePicker from '../../Input/InputDataPicker';
import InputInfo from '../../Input/InputInfo';
import InputInfoWithPopper from '../../Input/InputInfoWithPopper';
import InputSwitch from '../../Input/InputSwitch';
import InputText from '../../Input/InputText';
import { formUsersGridStyle, formAddActionsStyle } from './style/formAddActionsStyle';

const FIELDS_USER = ['responsible', 'accountable', 'consulted', 'informed'];

const FormAddAction = ({
  typeAction,
  register,
  watch,
  errors,
  control,
  setValue,
  clearErrors,
  initialValue,
  disabled,
}) => {
  const roles = useMemo(() => FIELDS_USER, []);
  const [openDialog, setOpenDialog] = useState(false);
  const [roleUser, setRoleUser] = useState(null);
  const [usersSelected, setUsersSelected] = useState({});
  const { t } = useTranslation();
  const { users } = UserManager();
  const { task } = ActionManager();

  const cssGridUsers = formUsersGridStyle();
  const css = formAddActionsStyle();

  useEffect(() => {
    setUsersSelected({});
    FIELDS_USER.forEach(user => register({ name: user }, { required: user === 'responsible' }));
    register({ name: 'automaticComplete' });
    setValue('automaticComplete', true);
  }, [register]);

  useEffect(() => {
    if (!isEmpty(initialValue)) {
      const { action } = initialValue;
      setValue('title', action.title);
      setValue('description', action.description);
      setValue('automaticComplete', action.automaticComplete);
      setValue('endDate', action.endDate);
      const obj = FIELDS_USER.reduce((acc, role) => {
        acc[role] = users[action[role]];
        return acc;
      }, {});
      setUsersSelected(obj);
      FIELDS_USER.forEach(role => setValue(role, action[role]));
    }
  }, [initialValue]);

  const handleDialogUsers = role => {
    setRoleUser(role);
    setOpenDialog(!openDialog);
  };

  const handleRemoveUser = role => {
    setValue(role, null);
    setRoleUser(role);
    const newState = { ...usersSelected };
    delete newState[role];
    setUsersSelected(newState);
  };

  const handleUserSelected = user => {
    setUsersSelected(prevState => ({ ...prevState, [roleUser]: user }));
    setValue(roleUser, user.fiscalCode.toUpperCase());
    clearErrors(roleUser);
  };

  const handleChangeSwitch = value => {
    setValue('automaticComplete', value);
  };

  return (
    <Grid container>
      {!disabled ? (
        <DialogUsers
          open={openDialog}
          handleClose={handleDialogUsers}
          handleUserSelected={handleUserSelected}
          usersSelected={usersSelected}
          role={roleUser}
        />
      ) : null}
      <Grid item xs={12}>
        <InputInfo
          title={
            typeAction === 'action'
              ? t('entities.update.actions.action')
              : t('entities.update.actions.subAction')
          }
        >
          <InputText
            name="title"
            placeholder={
              typeAction === 'action'
                ? t('entities.update.actions.actionPlaceholder')
                : t('entities.update.actions.subActionPlaceholder')
            }
            register={register({
              required: {
                value: true,
                message: t('validation.required'),
              },
              maxLength: {
                value: 125,
                message: t('validation.maxLenght', { maxLength: 125 }),
              },
            })}
            watch={watch}
            setValue={setValue}
            errors={errors}
            showCountCaracthers
            countCaracthers={125}
            disabled={disabled}
          />
        </InputInfo>
      </Grid>
      <Grid item xs={12}>
        <InputInfo title={t('entities.update.actions.description')}>
          <InputText
            name="description"
            placeholder={t('entities.update.actions.descriptionPlaceholder')}
            register={register({
              required: {
                value: true,
                message: t('validation.required'),
              },
              maxLength: {
                value: 500,
                message: t('validation.maxLenght', { maxLength: 500 }),
              },
            })}
            watch={watch}
            setValue={setValue}
            errors={errors}
            multiline
            rows={5}
            rowsMax={20}
            showCountCaracthers
            countCaracthers={500}
            disabled={disabled}
          />
        </InputInfo>
      </Grid>
      <Grid item xs={12}>
        <Box
          display="flex"
          flexDirection="row"
          flexWrap="nowrap"
          justifyContent="space-between"
          alignContent="flex-start"
          height="120px"
        >
          <Box paddingRight={1} height={1} width={0.5}>
            <InputInfo title={t('entities.update.actions.end')}>
              <Box border={0}>
                <InputDatePicker
                  control={control}
                  initialValue={task.startDate}
                  name="endDate"
                  setValue={setValue}
                  format="dd MMMM yyyy"
                  rules={{
                    required: {
                      value: true,
                      message: t('validation.required'),
                    },
                  }}
                  errors={errors}
                  disabled={disabled}
                  minDate={task.startDate}
                  maxDate={task.endDate}
                  invalidDateMessage={t('error.invalidDateMessage')}
                />
              </Box>
            </InputInfo>
          </Box>

          {typeAction === 'action' ? (
            <Box border={0} height={1} width={0.5}>
              <InputInfoWithPopper
                title={t('entities.update.actions.completion')}
                contentPopper={{
                  title: t('entities.update.actions.popper.title'),
                  description: t('entities.update.actions.popper.description'),
                }}
              >
                <Box border={0}>
                  <InputSwitch
                    label={t('entities.update.actions.automatic')}
                    on={t('entities.update.actions.yes')}
                    off={t('entities.update.actions.no')}
                    onChange={handleChangeSwitch}
                    disabled={disabled}
                  />
                </Box>
              </InputInfoWithPopper>
            </Box>
          ) : null}
        </Box>
      </Grid>

      <Grid item xs={12}>
        {roles.map(role => (
          <Grid key={role} item xs={12} classes={cssGridUsers}>
            <InputInfo title={t(`entities.update.actions.${role}`)}>
              <Box className={css.boxContainer}>
                <Box
                  className={`${css.borderBox} ${errors[role] ? 'error' : ''}`}
                  textAlign="left"
                  p={2}
                  display="flex"
                  flexDirection="row"
                  flexWrap="nowrap"
                  justifyContent="flex-start"
                  alignContent="flex-start"
                  alignItems="center"
                >
                  <Box marginRight={2}>
                    {!watch(role) ? (
                      <IconButton
                        fontSize="large"
                        className={css.boxAdd}
                        onClick={() => handleDialogUsers(role)}
                        disabled={disabled}
                      >
                        <AddIcon className={css.iconAdd} />
                      </IconButton>
                    ) : (
                      <Avatar
                        className={css.avatar}
                        alt={usersSelected[role]?.firstname}
                        src={usersSelected[role]?.image}
                        onClick={() => handleDialogUsers(role)}
                      />
                    )}
                  </Box>
                  <Box flexGrow={1}>
                    {!watch(role) ? null : (
                      <Typography variant="body1" color="textPrimary">
                        {`${usersSelected[role]?.firstname} ${usersSelected[role]?.lastname}`}
                      </Typography>
                    )}
                    <Typography variant="body2" color="textSecondary">
                      {!watch(role)
                        ? t('entities.update.actions.assignRole')
                        : usersSelected[role]?.role}
                    </Typography>
                  </Box>
                  {watch(role) ? (
                    <Box>
                      {!disabled ? (
                        <IconButton fontSize="large" onClick={() => handleDialogUsers(role)}>
                          <CreateIcon className={css.iconAdd} />
                        </IconButton>
                      ) : null}
                      {role !== 'responsible' ? (
                        <IconButton fontSize="large" onClick={() => handleRemoveUser(role)}>
                          <DeleteIcon className={css.iconAdd} />
                        </IconButton>
                      ) : null}
                    </Box>
                  ) : null}
                </Box>
              </Box>
              {errors[role] ? (
                <Box marginTop="3px" marginLeft="14px">
                  <Typography variant="caption" color="error" align="left">
                    {t('validation.required')}
                  </Typography>
                </Box>
              ) : null}
            </InputInfo>
          </Grid>
        ))}
      </Grid>
    </Grid>
  );
};

FormAddAction.propTypes = {
  typeAction: PropTypes.oneOf(['action', 'subAction']).isRequired,
  register: PropTypes.func.isRequired,
  watch: PropTypes.func.isRequired,
  errors: PropTypes.shape({}).isRequired,
  control: PropTypes.shape({}).isRequired,
  setValue: PropTypes.func.isRequired,
  clearErrors: PropTypes.func.isRequired,
  initialValue: PropTypes.shape({
    action: PropTypes.shape({
      title: PropTypes.string,
      description: PropTypes.string,
      automaticComplete: PropTypes.bool,
      endDate: PropTypes.string,
    }),
  }),
  disabled: PropTypes.bool.isRequired,
};

FormAddAction.defaultProps = {
  initialValue: {},
};

export default FormAddAction;
