import { useContext } from 'react';
import { useTranslation } from 'react-i18next';
import { apiUserFromFiscalCodeGet, apiUsersGet } from '../../api/users';
import { userMock } from '../../components/__mocks__/mocks';
import AppManager from '../app/AppManager';
import UserContext from './UserContext';

const UserManager = () => {
  // eslint-disable-next-line no-unused-vars
  const [state, setState] = useContext(UserContext);
  const { t } = useTranslation();
  const { useMock } = AppManager();
  const { serviceUrl, logged, usersPresent } = state;

  const setUsers = users => {
    setState(prevState => ({ ...prevState, users }));
  };

  const setUsersPresent = users => {
    setState(prev => ({ ...prev, usersPresent: [...new Set(prev.usersPresent.concat(users))] }));
  };

  const setPagination = pagination => setState({ ...state, pagination });

  const fetchUserPresent = async edoResponsible => {
    if (useMock) {
      const users = userMock.payload.reduce((acc, data) => {
        const user = data;
        const role = `${user.degree} ${user.edo ? '-' : ''} ${user?.edo?.name ?? ''}`;
        acc[user.fiscalCode.toUpperCase()] = {
          ...user,
          role,
        };
        return acc;
      }, {});
      setUsers(users);
    } else {
      const promises = usersPresent.map(user =>
        apiUserFromFiscalCodeGet(serviceUrl, user, user === edoResponsible)
      );
      Promise.all(promises).then(json => {
        const users = json.reduce((acc, data) => {
          const user = data.payload;
          const role = `${user.degree} ${user.edo ? '-' : ''} ${user?.edo?.name ?? ''}`;
          acc[user.fiscalCode.toUpperCase()] = {
            ...user,
            role,
          };
          return acc;
        }, {});
        setUsers(users);
      });
    }
  };

  const fetchUser = async (pagination, search, informedOrConsulted) => {
    try {
      let json;

      if (useMock) {
        json = await new Promise(resolve => setTimeout(() => resolve(userMock)), 100);
      } else {
        json = await apiUsersGet(serviceUrl, pagination, search, informedOrConsulted);
      }
      const {
        payload,
        totalPages,
        totalElements,
        numberOfElements,
        number,
        size,
        first,
        last,
      } = json;
      if (payload) {
        const users = payload.reduce((acc, user) => {
          const role = `${user.degree} ${user.edo ? '-' : ''} ${user?.edo?.name ?? ''}`;
          acc[user.fiscalCode.toUpperCase()] = {
            ...user,
            role,
          };
          return acc;
        }, {});

        const userLogged = state.users[logged];
        const userLoggedPresent = Object.keys(users).find(
          key => users[key]?.fiscalCode.toUpperCase() === logged
        );

        setState(prev => ({
          ...prev,
          users: userLoggedPresent
            ? users
            : { ...users, [userLoggedPresent?.fiscalCode.toUpperCase()]: userLogged },
          pagination: {
            totalPages,
            totalElements,
            numberOfElements,
            number,
            size,
            first,
            last,
          },
        }));
      }
    } catch (e) {
      setState({ ...state, error: t('error.dataLoading'), message: null });
    }
  };

  const setError = error => setState(prev => ({ ...prev, error }));

  const clearError = () => setState(prev => ({ ...prev, error: null }));

  const setMessage = message => setState(prev => ({ ...prev, message }));

  const clearMessage = () => setState(prev => ({ ...prev, message: null }));

  return {
    logged: state.logged,
    users: state.users,
    serviceUrl: state.serviceUrl,
    useMock: state.useMock,
    setUsers,
    setUsersPresent,
    setPagination,
    fetchUserPresent,
    fetchUser,
    setError,
    clearError,
    setMessage,
    clearMessage,
  };
};

export default UserManager;
