import DateFnsUtils from '@date-io/date-fns';
import { Box, Button, Container, Grid, Typography } from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';
import CachedIcon from '@material-ui/icons/Cached';
import LibraryBooksIcon from '@material-ui/icons/LibraryBooks';
import { MuiPickersUtilsProvider } from '@material-ui/pickers';
import itLocale from 'date-fns/locale/it';
import { cloneDeep, isEmpty, omit } from 'lodash';
import PropTypes from 'prop-types';
import React, { useEffect, useRef, useState } from 'react';
import { useForm } from 'react-hook-form';
import { Trans, useTranslation } from 'react-i18next';
import ActionManager from '../../context/action/ActionManager';
import AppManager from '../../context/app/AppManager';
import UserManager from '../../context/user/UserManager';
import ActionInfo from './ActionInfo';
import FormAddAction from './FormAddAction/FormAddAction';
import { actionsStyle } from './style/actionsStyle';

const initAction = {
  title: 'Azione',
  description: '',
  startDate: null,
  automaticComplete: true,
  responsible: '',
  accountable: null,
  consulted: null,
  informed: null,
  subActions: [],
  status: 'CREATED',
};

const FIELDS = [
  'title',
  'description',
  'startDate',
  'endDate',
  'responsible',
  'accountable',
  'consulted',
  'informed',
];

const Actions = ({ onSubmit }) => {
  const [disableAddAction, setDisableAddAction] = useState(false);
  const [disableAddSubAction, setDisableAddSubAction] = useState(false);
  const [actions, setActions] = useState([]);
  const [typeAction, setTypeAction] = useState(null);
  const [info, setInfo] = useState({});
  const [disabledForm, setDisabledForm] = useState(true);
  const [showForm, setShowForm] = useState(false);
  const [isFirstInsertSubAction, setFirstInsertSubAction] = useState(true);
  const [indexAction, setIndexAction] = useState(0);
  const [validationSubActions, setValidationSubActions] = useState(false);
  const { useMock } = AppManager();
  const { task, contextActions } = ActionManager();
  const { fetchUser, fetchUserPresent } = UserManager();

  const {
    register,
    handleSubmit,
    errors,
    watch,
    control,
    setValue,
    trigger,
    clearErrors,
    reset,
  } = useForm({
    mode: 'onChange',
    reValidateMode: 'onBlur',
    defaultValues: { initAction },
  });

  const css = actionsStyle();
  const { t } = useTranslation();

  const usePreviousValues = values => {
    const ref = useRef();
    useEffect(() => {
      ref.current = values;
    });
    return ref.current;
  };

  const preWatch = usePreviousValues(watch(FIELDS));

  const handleClickAddAction = async () => {
    if (!showForm) {
      setShowForm(true);
    }
    setDisabledForm(false);
    setInfo({});
    if (actions.length > contextActions.length || typeAction === 'subAction') {
      await trigger(FIELDS);
    }
    if (isEmpty(errors)) {
      const newActions = cloneDeep(actions);
      setIndexAction(newActions.length);
      setTypeAction('action');
      newActions.push({ ...initAction });
      setActions(newActions);
      if (actions.length) {
        reset(FIELDS);
      }
      setValue('startDate', task.startDate);
    }
  };

  const handleClickAddSubAction = async (type, index) => {
    if (!showForm) {
      setShowForm(true);
    }
    setDisabledForm(false);
    setInfo({});
    if (validationSubActions) {
      await trigger(FIELDS);
    }

    if (isEmpty(errors)) {
      setIndexAction(index);
      const newActions = cloneDeep(actions);
      reset(FIELDS);
      setTypeAction(type);
      const initSubAction = { ...omit({ ...initAction }, 'subActions', 'automaticComplete') };
      initSubAction.title = 'Sottoazione';
      newActions[index].subActions.push({ ...initSubAction, endDate: actions[index].endDate });
      setActions(newActions);
      setFirstInsertSubAction(false);
      setDisableAddSubAction(true);
      setValue('endDate', actions[index].endDate);
      setValidationSubActions(true);
    }
  };

  const handleLoadInfo = (type, action, index) => {
    setShowForm(true);
    if (!disabledForm) {
      setDisabledForm(true);
    }
    setInfo({ type, action, index });
  };

  useEffect(() => {
    fetchUser();
  }, []);

  useEffect(() => {
    if (contextActions.length) {
      fetchUserPresent();
      setActions(contextActions);
    }
  }, [contextActions]);

  useEffect(() => {
    if (preWatch && !disabledForm) {
      if (typeAction === 'subAction' && isFirstInsertSubAction) return;
      const fields = watch(FIELDS);
      const changedField = Object.keys(fields).filter(key => fields[key] !== preWatch[key]);
      if (changedField.length) {
        const newActions = cloneDeep(actions);
        const fieldsChanged = changedField.reduce((acc, key) => {
          acc[key] = fields[key];
          return acc;
        }, {});
        if (!isEmpty(fieldsChanged)) {
          if (typeAction === 'action') {
            newActions[indexAction] = { ...newActions[indexAction], ...fieldsChanged };
          } else if (typeAction === 'subAction') {
            const indexSubAction = newActions[indexAction]?.subActions?.length - 1;
            newActions[indexAction].subActions[indexSubAction] = {
              ...newActions[indexAction].subActions[indexSubAction],
              ...fieldsChanged,
            };
          }
          setActions(newActions);
          setDisableAddAction(false);
          setDisableAddSubAction(false);
        }
      }
    }
  }, [watch, preWatch, typeAction, disabledForm]);

  const onSubmitInternal = (data, event) => {
    if (isEmpty(errors) && actions.length) {
      onSubmit(actions, event);
      setInfo({});
      setDisabledForm(true);
    }
  };

  if (!isEmpty(errors)) {
    console.log('errors', errors);
  }

  return (
    <MuiPickersUtilsProvider utils={DateFnsUtils} locale={itLocale}>
      <form onSubmit={handleSubmit(onSubmitInternal)}>
        <Box className={css.boxGrid}>
          <Grid container className={css.gridContainer}>
            <Grid item xs={6} classes={{ item: css.item }}>
              <Box className={css.borderBox}>
                <Button
                  variant="contained"
                  type="button"
                  className={css.buttonAddAction}
                  startIcon={<AddIcon />}
                  onClick={() => handleClickAddAction('new')}
                  disabled={disableAddAction}
                >
                  <Typography variant="body2" color="textSecondary">
                    {t('entities.update.actions.add')}
                  </Typography>
                </Button>
              </Box>

              {actions && actions.length > 0 ? (
                <Container fixed disableGutters className={css.containerInfo}>
                  <Container fixed disableGutters className={css.containerInfoScroll}>
                    <Box className={`${css.borderBox} ${css.borderBoxInfo}`}>
                      <Box className={css.boxActions}>
                        <ActionInfo
                          actions={actions}
                          handleClickAction={handleClickAddSubAction}
                          disabledButton={disableAddSubAction}
                          loadInfo={handleLoadInfo}
                          disabledEvents={!disabledForm}
                          infoSelected={info}
                        />
                      </Box>
                    </Box>
                  </Container>
                </Container>
              ) : null}
            </Grid>
            <Grid item xs={6} classes={{ item: css.item }}>
              {!showForm ? (
                <Box className={css.boxEmptyActionContainer}>
                  <Box className="boxIcon">
                    <LibraryBooksIcon />
                  </Box>
                  <Box>
                    <Typography variant="body1" color="textPrimary">
                      {t('entities.update.actions.updateTaskAction')}
                    </Typography>
                  </Box>
                  <Box>
                    <Typography variant="body1" color="textSecondary">
                      <Trans t={t} i18nKey="entities.update.actions.updateTaskActionDescription">
                        {[<span key={0} className={css.highlight} />]}
                      </Trans>
                    </Typography>
                  </Box>
                </Box>
              ) : (
                <Container fixed disableGutters className={css.containerInfo}>
                  <Container fixed disableGutters className={css.containerInfoScroll}>
                    <Box p={1} className={css.border}>
                      <FormAddAction
                        typeAction={typeAction ?? 'action'}
                        register={register}
                        control={control}
                        watch={watch}
                        errors={errors}
                        setValue={setValue}
                        clearErrors={clearErrors}
                        useMock={useMock}
                        initialValue={info}
                        disabled={disabledForm}
                      />
                    </Box>
                  </Container>
                </Container>
              )}
            </Grid>
            <Grid item xs={12}>
              <Box p={2} border={0} textAlign="center" className={css.boxButtons}>
                <Button
                  size="large"
                  type="submit"
                  endIcon={<CachedIcon />}
                  variant="outlined"
                  className={css.button}
                  disabled={disabledForm}
                >
                  <Typography variant="body2" color="textSecondary">
                    {t('entities.update.buttons.update')}
                  </Typography>
                </Button>
              </Box>
            </Grid>
          </Grid>
        </Box>
      </form>
    </MuiPickersUtilsProvider>
  );
};

Actions.propTypes = {
  onSubmit: PropTypes.func.isRequired,
};

export default Actions;
