import { Box, Container, Paper, Typography } from '@material-ui/core';
import React from 'react';
import { Trans, useTranslation } from 'react-i18next';
import PropTypes from 'prop-types';
import AuthenticatedView from '../../auth/KeycloakViews';
import ActionManager from '../../context/action/ActionManager';
import AppManager from '../../context/app/AppManager';
import LoadingManager from '../../context/loading/LoadingManager';
import Notification from '../common/Notification';
import Spinner from '../Spinner/Spinner';
import Actions from './Actions';

import containerStyle from './style/taskUpdatePageStyle';

const TaskUpdatePage = ({ taskId }) => {
  const { keycloak, serviceUrl } = AppManager();
  const { addAction, error, clearError, message, clearMessage } = ActionManager(
    keycloak,
    serviceUrl
  );

  const classesContainer = containerStyle();

  const onSubmit = async (formData, event) => {
    formData.forEach(d => console.table(d));
    if (event) {
      event.preventDefault();
      event.stopPropagation();
    }
    await addAction(formData);
  };

  const { isLoading } = LoadingManager();
  const { t } = useTranslation();

  return (
    <AuthenticatedView keycloak={keycloak}>
      <Paper elevation={5}>
        {isLoading ? <Spinner /> : null}
        <Container maxWidth="xl" disableGutters className={classesContainer.container}>
          <Box p={2}>
            <Typography variant="h6">
              <Trans t={t} i18nKey="entities.update.actions.task" count={taskId}>
                {[<b key={1}> {{ taskId }} </b>]}
              </Trans>
            </Typography>
          </Box>
          <Actions onSubmit={onSubmit} />
          {message === null && error === null ? null : (
            <Notification
              message={message || error}
              onClose={() => (message ? clearMessage() : clearError())}
              status={message ? 'success' : 'error'}
              anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
            />
          )}
        </Container>
      </Paper>
    </AuthenticatedView>
  );
};

TaskUpdatePage.propTypes = {
  taskId: PropTypes.string.isRequired,
};

export default TaskUpdatePage;
